.tiles-container {
	position: relative;
	margin-bottom: 25px;
	padding-right: -5px !important;
}

.tiles-container .tile {
	padding: 0 5px 10px 5px !important;
}

.tiles-container .tile .post {
	margin-bottom: 0;
}

.tiles-container .ias-btn {
	position: absolute;
	bottom: -20px;
	width: 100%;
	cursor: pointer;
}

.load_more {
	font-size: 1rem;
	font-weight: bold;
	margin-top: 10px;
	padding: 10px;
}

.load_more a {
	text-decoration: none;
}

.load_more a:hover {
	opacity: 0.7;
}